
import { defineComponent } from 'vue';
import ChatForm from './ChatForm.vue';
import ChatMessagesList from './ChatMessagesList.vue';

interface MessageState {
  author: string,
  body: string,
}

interface DataState {
  errored: boolean;
  loading: boolean;
  messages: Array<MessageState>;
}

export default defineComponent({
  components: {
    ChatMessagesList,
    ChatForm,
  },
  data(): DataState {
    return {
      errored: false,
      loading: false,
      messages: [],
    };
  },
  methods: {
    onFormSubmit(prompt: string) {
      this.messages.push({
        author: 'user',
        body: prompt,
      });
      this.loading = true;
      this.scrollDown();
    },
    onSuccess(response: string) {
      this.messages.push({
        author: 'ai',
        body: response,
      });
      this.loading = false;
      this.scrollDown();
    },
    onError() {
      this.errored = true;
    },
    scrollDown() {
      console.log('SCROLL DOWN', window, document);
      setTimeout(() => window.scrollTo(0, document.body.scrollHeight), 1);
    },
  },
});
